import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Employee } from '../shared/employee.model';
import { EmployeeService } from '../shared/employee.service';
import { Notyf } from 'notyf';

const notyf = new Notyf({
  duration: 4000, // 4 seconds
  position: {
    x: 'right', // 'left' | 'right'
    y: 'top', // 'top' | 'bottom'
  },
  dismissible: true, // Allow manual close
});

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss'],
})
export class EmployeeComponent implements OnInit {
  empForm: FormGroup;
  showModal: boolean = false;
  editMode: boolean = false;

  employees: Employee[];
  selectedEmployee: Employee;

  filteredEmployees: Employee[] = []; // This will hold the filtered data

  searchTerm: string = '';
  selectedPosition: string = '';

  constructor(private fb: FormBuilder, private _empService: EmployeeService) {}
  isListView = false;
  isDataLoaded = false;
  ngOnInit(): void {
    this.getEmployees();

    this.empForm = this.fb.group({
      _id: [''],
      image: [''],
      name: ['', Validators.required],
      position: ['', Validators.required],
      office: ['', Validators.required],
      salary: ['', Validators.required],
      about: [''],
    });
  }

  colors: string[] = [
    '#DAA520', // Goldenrod
    '#55a362', // Steel blue
    '#F44336', // Tomato red
    '#CDDC39', // Lime green
    '#FF5722', // Blue violet
    '#FF6347', // Tomato red
    '#AF4C4C', // Green
    '#2196F3', // Blue
    '#FFC107', // Amber
    '#9C27B0', // Purple
    '#DAA520', // Goldenrod
    '#55a362', // Steel blue
    '#F44336', // Lime red
    '#CDDC39', // Lime green
    '#FF5722', // Blue violet
  ];

  getColor(): string {
    const randomColor =
      this.colors[Math.floor(Math.random() * this.colors.length)];
    return randomColor;
  }
  uniquePositions: string[] = [];

  getEmployees() {
    this._empService.getEmployeeList().subscribe(
      (res) => {
        console.log(res);
        this.employees = res as Employee[];
        this.employees.forEach((element) => {
          element.bg = this.getColor();
        });
        this.filteredEmployees = [...this.employees];
        this.extractUniquePositions();
        this.isDataLoaded = true;
      },
      (err) => {
        console.log(err);
      }
    );
  }

  extractUniquePositions(): void {
    this.uniquePositions = [
      ...new Set(this.employees.map((emp) => emp.position)),
    ];

    console.log(this.uniquePositions);
  }

  onEmpSubmit() {
    debugger;
    if (this.empForm.valid) {
      debugger;
      console.log(this.empForm.value);

      if (this.editMode) {
        this._empService.putEmployee(this.empForm.value).subscribe(
          (res) => {
            notyf.success({
              message: 'Updated successfully!',
              duration: 5000, // 5 seconds
              dismissible: true,
            });
            console.log('Updated successfully');
            this.getEmployees();
            this.editMode = false;
          },
          (err) => {
            console.log(err);
          }
        );
      } else {
        this._empService.postEmployee(this.empForm.value).subscribe(
          (res) => {
            notyf.success({
              message: 'Saved successfully!',
              duration: 5000, // 5 seconds
              dismissible: true,
            });
            console.log('Saved successfully');
            this.getEmployees();
          },
          (err) => {
            console.log(err);
          }
        );
      }

      this.empForm.reset();
      this.onCloseModal();
    } else {
      let key = Object.keys(this.empForm.controls);
      // console.log(key);

      key.filter((data) => {
        // console.log(data);
        let control = this.empForm.controls[data];
        // console.log(control);
        if (control.errors != null) {
          control.markAsTouched();
        }
      });
    }
  }

  onEditEmployee(emp: Employee) {
    this.editMode = true;
    this.empForm.reset();
    console.log(emp);
    this.showModal = true;
    this.selectedEmployee = emp;
    console.log(this.selectedEmployee);
    this.empForm.patchValue(this.selectedEmployee);
  }

  onDeleteEmployee(id) {
    if (confirm('Do you want to delete this employee?')) {
      // console.log(id);
      this._empService.deleteEmployee(id).subscribe(
        (res) => {
          notyf.success({
            message: 'Deleted successfully!',
            duration: 5000, // 5 seconds
            dismissible: true,
          });
          this.getEmployees();
        },
        (err) => {
          console.log(err);
        }
      );
    }
  }

  onAddEmployee() {
    this.editMode = false;
    this.empForm.reset();
    this.showModal = true;
  }

  onCloseModal() {
    this.showModal = false;
  }

  filterEmployees() {
    this.filteredEmployees = this.employees.filter((emp) => {
      const matchesName = emp.name
        .toLowerCase()
        .includes(this.searchTerm.toLowerCase());
      const matchesPosition = this.selectedPosition
        ? emp.position === this.selectedPosition
        : true;
      return matchesName && matchesPosition;
    });
  }

  byteString: string | null = null;

  triggerFileInput() {
    const fileInput = document.getElementById('fileInput') as HTMLInputElement;
    fileInput.click(); // Trigger the file input click event
  }

  onFileSelected(event: any) {
    const file = event.target.files[0];
    if (!file) return;

    const reader = new FileReader();
    reader.readAsDataURL(file); // Convert file to Base64
    reader.onload = () => {
      this.byteString = reader.result as string; // Store Base64 string
      this.empForm.patchValue({ image: this.byteString });
      console.log('Updated Byte String:', this.byteString);
    };
    reader.onerror = (error) => {
      console.error('Error converting file:', error);
    };
  }
}
